ionan información adicional. Los videos, por su parte, suelen ir acompañados de un comentario. El uso de fotografía y video se ha vuelto cada vez más común dentro de M&E en los últimos años. Esto se debe, en parte, a las mejoras en la tecnología de la telefonía móvil, que ha permitido cada vez más a las personas producir productos audiovisuales baratos y de alta calidad.")]),_vm._m(10),_vm._m(11),_c('p',{staticClass:"mb-5 mt-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Un estudio de caso no es una herramienta de recopilación de datos en sí mismo. Es un trabajo descriptivo que puede proporcionar información detallada sobre un tema. A menudo, se basa en información adquirida a través de una o más de las otras herramientas descritas en este documento, como entrevistas u observación. Los estudios de casos suelen estar escritos, pero también pueden presentarse como fotografías, películas o videos. Los estudios de caso a menudo se centran en las personas (individuos, hogares, comunidades).")]),_vm._m(12),_c('p',{staticClass:"mb-5 mt-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Pero también pueden centrarse en cualquier otra unidad de análisis, como ubicaciones, organizaciones, políticas o el medio ambiente. Las historias de cambio son similares a los estudios de casos. Sin embargo, tienen un enfoque específico en el cambio y, por lo general, solo se desarrollan después de que un proyecto o programa ha comenzado.")]),_vm._m(13),_c('p',{staticClass:"mb-5 mt-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Están diseñados para recopilar y registrar información de muchas personas, grupos u organizaciones, de manera coherente. Un cuestionario es un formulario que contiene preguntas. Puede ser un formulario impreso o uno diseñado para ser llenado en línea. Los cuestionarios se pueden administrar de muchas formas diferentes. Una encuesta, por el contrario, es normalmente un gran ejercicio formal.")]),_vm._m(14),_c('p',{staticClass:"mb-5 mt-5",attrs:{"data-aos":"fade-right"}},[_vm._v